import './App.css';
// ./App.module.css 로 바꾸면 영향 안받게 App.js에서만 적용되게 가능
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Navbar, Container, Nav } from 'react-bootstrap';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import bg from './img/bg.png';
import { lazy, Suspense, createContext, useEffect, useState } from 'react';
//import {a,b} from './data.js';
import data from './data.js';
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom';
//import Detail from './page/Detail.js';
import axios from 'axios'
//import Cart from './page/Cart.js'
import { useQuery } from "react-query"

const Detail = lazy(() => import('./page/Detail.js'));
const Cart = lazy(() => import('./page/Cart.js'));
//성능개선 : 사이트 발행할때도 별도의 js파일로 분리됨

export let Context1 = createContext() //state 보관함


function App() {

  // let obj = { name: 'kim' }
  // //JSON.stringify(obj) object자료 localstorage에 json으로 바꿔서넣을때
  // localStorage.setItem('data', JSON.stringify(obj))
  // let 꺼낸거 = localStorage.getItem('data');

  //console.log(JSON.parse(꺼낸거).name) //JSON=>array/object 변환


  let [재고] = useState([10, 11, 12]);

  let [shoes, shoesSet] = useState(data);

  let navigate = useNavigate();

  let result = useQuery('작명', () =>
    // 장점1. 성공/실패/로딩중 쉽게 파악 가능
    axios.get('https://codingapple1.github.io/userdata.json') //장점3. 실패시 retry 알아서 해줌
      .then((a) => {
        console.log('요청됨') //장점2. 틈만나면 자동으로 재요청
        return a.data
      }),
    { staleTime: 2000 }
  )
  //state 공유 안해도됨
  //  result.data
  //  result.isLoading        로딩중일때 '로딩중입니다'보여주고싶으면
  //  result.error
  let [getcheck, checkSet] = useState(false);

  let [url] = useState(['https://codingapple1.github.io/shop/data2.json', 'https://codingapple1.github.io/shop/data3.json'])
  let [btncount, countSet] = useState(0);

  let copy = [...shoes];

  let [load, setLoad] = useState(-1);

  var 수정한거 = JSON.parse(localStorage.getItem('watched'));


  useEffect(() => {
    if (수정한거 === null) {
      setLoad(-1);
      localStorage.setItem('watched', JSON.stringify([]))
    }
    else {
      setLoad(0);
    }

  }, [])
  //이미 watched항목이 있으면 setItem하지 말아주세요 ~ 하기 
  return (
    <div className="App">
      <div>
        <Navbar bg="light" variant="light">
          <Container>
            <Navbar.Brand href='/' style={{ color: 'green' }}>shoeShop</Navbar.Brand>
            <Nav className="me-auto">
              <Nav.Link onClick={() => { navigate(-1) }}>Undo</Nav.Link>
              <Nav.Link onClick={() => { navigate('/') }}>Home</Nav.Link>
              <Nav.Link onClick={() => { navigate('/cart') }}>Cart</Nav.Link>
              <Nav.Link onClick={() => { navigate(1) }}>redo</Nav.Link>
              {/* <Link to="/" className="sub-link">Home</Link>
            <Link to="/detail" className="sub-link">Cart</Link> */}
            </Nav>
            <Nav className="ms-auto">
              {/* {result.isLoading ? '로딩중' : result.data.name} */}
              {result.isLoading && '로딩중'}
              {result.error && '에러남'}
              {result.data && result.data.name}
            </Nav>
          </Container>
        </Navbar>
      </div>


      <div className='basket'>
        <button style={{ float: 'left' }} onClick={() => {
          document.querySelector('.basket').classList.toggle('move');
        }}>-</button>
        <h5>최근 본 상품</h5>

        <Container>
          {
            load == -1 ? <p>비어있음</p> : 수정한거.map((a, i) => {

              return (

                <Row key={i}>
                  <Col>
                    <Link to={`/detail/${a.id}`}>
                      <img src={`https://codingapple1.github.io/shop/shoes${Number(a.id) + 1}.jpg`} width="100px" />
                    </Link>
                    <p>{a.title}</p>
                  </Col>
                </Row>
              );
            })

          }
        </Container>

      </div>
      <Suspense fallback={<div>로딩중임</div>}>
        <Routes>
          <Route path="/" element={<>
            <div className="main-bg" style={{ backgroundImage: `url(${bg})` }}></div>

            <Button onClick={() => {

              copy.sort((a, b) => a.title.toLowerCase() < b.title.toLowerCase() ? -1 : 1);
              shoesSet(copy);
            }}>가나다정렬</Button>
            <Things shoes={shoes} />
            {
              getcheck === true ? null : <Button onClick={() => {

                btncount === 1 ? checkSet(true) : checkSet(false);
                countSet(btncount + 1);
                axios.get(url[btncount])
                  //</>Promise.all([axios.get('https://codingapple1.github.io/shop/data2.json').axios.get('https://codingapple1.github.io/shop/data3.json')])
                  .then((결과) => {
                    let copy = [...shoes];
                    결과.data.map((data) => {
                      copy.push(data);
                    }) // = let copy = [...shoes, ...결과.data]; >>ㄹㅇ 킹받네
                    shoesSet(copy);

                    // 로딩중UI숨기기
                    console.log(copy);
                  })
                  .catch(() => {
                    // 로딩중UI숨기기
                    console.log('경로잘못되었스요~');
                  })
                //서버에 데이터 전송하는법  axios.post('/sdfsdf',{name:'kim'});
              }}>버튼</Button>
            }

          </>} />


          <Route path="/detail/:id" element={

            <Context1.Provider value={{ 재고, shoes }}>
              <Detail shoes={shoes} />
            </Context1.Provider>

          } />

          <Route path="/cart" element={<Cart />} />

          <Route path="*" element={<div>없는 페이지에요</div>} />

          <Route path="/about" element={<About />} >
            <Route path="member" element={<div>멤버임</div>} />
            <Route path="location" element={<div>위치정보임</div>} />
          </Route>
          {/* 위에가 nested route <Route path="/about/member" element={ <About /> } />
        <Route path="/about/location" element={ <About /> } /> */}
          <Route path="/event" element={<Event />}>
            <Route path="one" element={<p>첫 주문시 양배추즙 서비스</p>} />
            <Route path="two" element={<p>생일기념 쿠폰받기</p>} />
          </Route>
        </Routes>
      </Suspense>
      {/* <img src={process.env.PUBLIC_URL + '/logo192.png'} width="80%" /><h4>{shoes[0].title}</h4> <p>{shoes[0].price}</p> */}


    </div>
  );
}
function Event() {
  return (
    <div>
      <h4>오늘의 이벤트</h4>
      <Outlet></Outlet>
    </div>
  )
}
function About() {
  return (
    <div>
      <h4>회사정보임</h4>
      <Outlet></Outlet>
    </div>
  )
}

function Things(props) {
  return (
    <>

      <Container>
        <Row>

          {
            props.shoes.map((a, i) => {

              return (<Col md={4} key={i}>
                <Link to={`/detail/${a.id}`}>
                  <img src={`https://codingapple1.github.io/shop/shoes${Number(a.id) + 1}.jpg`} width="80%" />
                </Link>
                <h4>{a.title}</h4>
                <p>{a.price}</p>
              </Col>);
            })
          }
        </Row>
      </Container>
    </>
  )
}


export default App;
